  <div class="insights-management">
    <!-- 页面头部 - 高端设计 -->
    <div class="page-header-wrapper">
      <div class="header-background"></div>
      <div class="header-content">
        <div class="header-title">
          <div class="title-icon">
            <BulbOutlined />
