            </a-button>
          </a-space>
        </div>
      </div>
    </div>

    <!-- 核心统计指标 - 突出显示 -->
    <div class="stats-overview">
      <a-row :gutter="[24, 24]">
        <a-col :xs="24" :sm="12" :xl="6">
          <div class="premium-stat-card insights-card">
            <div class="stat-icon">
              <EyeOutlined />
