              <div class="stat-trend">
                <span class="trend-indicator positive">↗ +12%</span>
              </div>
            </div>
          </div>
        </a-col>
        <a-col :xs="24" :sm="12" :xl="6">
          <div class="premium-stat-card priority-card">
            <div class="stat-icon">
              <ExclamationCircleOutlined />
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ highPriorityInsights.length || 0 }}</div>
              <div class="stat-label">高优先级洞察</div>
              <div class="stat-trend">
                <span class="trend-indicator warning">⚠ 需关注</span>
              </div>
            </div>
          </div>
        </a-col>
        <a-col :xs="24" :sm="12" :xl="6">
          <div class="premium-stat-card actions-card">
            <div class="stat-icon">
              <CheckCircleOutlined />
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ allActionPlans.length || 0 }}</div>
              <div class="stat-label">执行计划数</div>
              <div class="stat-trend">
                <span class="trend-indicator positive">✓ 就绪</span>
              </div>
            </div>
          </div>
        </a-col>
        <a-col :xs="24" :sm="12" :xl="6">
          <div class="premium-stat-card urgent-card">
            <div class="stat-icon">
              <ClockCircleOutlined />
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ urgentActionPlans.length || 0 }}</div>
              <div class="stat-label">紧急任务</div>
              <div class="stat-trend">
                <span class="trend-indicator urgent">🔥 紧急</span>
              </div>
            </div>
          </div>
        </a-col>
      </a-row>
    </div>

    <!-- 主内容区 - 洞察为主，数据源为辅 -->
    <div class="main-content-area">
      <a-row :gutter="[32, 32]">
        <!-- 主要洞察展示区域 -->
        <a-col :xs="24" :lg="16" :xl="18">
          <div class="insights-main-panel">
            <InsightsPanel />
          </div>
        </a-col>
        
        <!-- 辅助数据源面板 -->
        <a-col :xs="24" :lg="8" :xl="6">
          <div class="data-source-sidebar">
            <a-card class="data-source-card" :bordered="false">
              <template #title>
                <div class="card-title">
                  <DatabaseOutlined />
