                </div>
              </template>
              <template #extra>
                <a-button type="link" size="small" @click="showDataConfig = true">
                  <SettingOutlined />
                </a-button>
              </template>
              
              <div class="data-source-content">
                <!-- 数据统计 -->
                <div class="data-stats">
                  <div class="stats-grid">
                    <div class="stat-item">
                      <div class="stat-number">{{ feedbackStats.total_feedback || 0 }}</div>
                      <div class="stat-desc">反馈总数</div>
                    </div>
                    <div class="stat-item">
                      <div class="stat-number">{{ feedbackStats.high_priority_count || 0 }}</div>
                      <div class="stat-desc">高优先级</div>
                    </div>
                  </div>
                  
                  <div class="sentiment-distribution">
                    <div class="sentiment-item positive">
                      <span class="sentiment-dot"></span>
                      正面: {{ feedbackStats.sentiment_distribution?.positive || 0 }}
                    </div>
                    <div class="sentiment-item negative">
                      <span class="sentiment-dot"></span>
                      负面: {{ feedbackStats.sentiment_distribution?.negative || 0 }}
                    </div>
                    <div class="sentiment-item neutral">
                      <span class="sentiment-dot"></span>
                      中性: {{ feedbackStats.sentiment_distribution?.neutral || 0 }}
                    </div>
                  </div>
                </div>
                
                <!-- 操作按钮 -->
                <div class="data-actions">
                  <a-button block type="dashed" @click="refreshFeedbackData" class="action-btn">
                    <DatabaseOutlined />
                    刷新反馈数据
                  </a-button>
                  <a-button block type="dashed" @click="showFeedbackSelector = true" class="action-btn">
                    <SelectOutlined />
                    选择反馈数据
                  </a-button>
                </div>
                
                <!-- 最近更新时间 -->
                <div class="last-update">
                  <small>最近更新: {{ formatTime(insightsStats.last_generation_time) }}</small>
                </div>
              </div>
            </a-card>
          </div>
        </a-col>
      </a-row>
    </div>

    <!-- 分析详情区域 - 精致设计 -->
    <div class="analysis-details-section">
      <a-row :gutter="[32, 32]">
        <!-- 洞察分类视图 -->
        <a-col :xs="24" :lg="8">
          <div class="analysis-card category-analysis">
            <div class="card-header">
              <div class="header-icon">
                <LineChartOutlined />
              </div>
              <div class="header-title">洞察分类分析</div>
            </div>
            <div class="card-content">
              <div class="category-list">
                <div 
                  v-for="(items, type) in insightsByType" 
                  :key="type"
                  class="category-item"
                  @click="selectedCategory = type"
                  :class="{ active: selectedCategory === type }"
                >
                  <div class="category-info">
                    <div class="category-icon">
                      <component :is="getInsightIcon(type)" />
                    </div>
                    <div class="category-details">
                      <div class="category-name">{{ getInsightTypeText(type) }}</div>
                      <div class="category-count">{{ items.length }} 个洞察</div>
                    </div>
                    <div class="category-badge">
                      <a-badge 
                        :count="items.length" 
                        :style="{ backgroundColor: getInsightTypeColor(type) }"
                      />
                    </div>
                  </div>
                  <div class="category-progress">
                    <a-progress 
                      :percent="allInsights.length > 0 ? Math.round((items.length / allInsights.length) * 100) : 0" 
                      size="small"
                      :stroke-color="getInsightTypeColor(type)"
                      :show-info="false"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </a-col>

        <!-- 优先级分布 -->
        <a-col :xs="24" :lg="8">
          <div class="analysis-card priority-analysis">
            <div class="card-header">
              <div class="header-icon">
                <TrophyOutlined />
              </div>
              <div class="header-title">优先级分布</div>
            </div>
            <div class="card-content">
              <div class="priority-list">
                <div 
                  v-for="(plans, priority) in actionPlansByPriority" 
                  :key="priority"
                  class="priority-item"
                >
                  <div class="priority-header">
                    <a-tag :color="getPriorityColor(priority)" class="priority-tag">
                      {{ priority }}
                    </a-tag>
                    <span class="priority-count">{{ plans.length }} 个计划</span>
                  </div>
                  <div class="priority-plans">
                    <div 
                      v-for="plan in plans.slice(0, 2)" 
                      :key="plan.plan_id"
                      class="plan-preview"
                    >
                      <div class="plan-title">{{ plan.title }}</div>
                    </div>
                    <div v-if="plans.length > 2" class="more-plans">
                      <a-button type="link" size="small">
                        查看全部 {{ plans.length }} 个计划
                      </a-button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </a-col>

        <!-- 最近活动时间线 -->
        <a-col :xs="24" :lg="8">
          <div class="analysis-card activity-analysis">
            <div class="card-header">
              <div class="header-icon">
                <ClockCircleOutlined />
              </div>
              <div class="header-title">最近活动</div>
            </div>
            <div class="card-content">
              <div class="activity-timeline">
                <div class="timeline-item" v-if="allInsights.length > 0">
                  <div class="timeline-dot insights"></div>
                  <div class="timeline-content">
                    <div class="timeline-title">生成洞察分析</div>
                    <div class="timeline-desc">成功生成 {{ allInsights.length }} 个智能洞察</div>
                    <div class="timeline-time">{{ formatTime(insightsStats.last_generation_time) }}</div>
                  </div>
                </div>
                <div class="timeline-item" v-if="allActionPlans.length > 0">
                  <div class="timeline-dot actions"></div>
                  <div class="timeline-content">
                    <div class="timeline-title">制定执行计划</div>
                    <div class="timeline-desc">创建 {{ allActionPlans.length }} 个可执行计划</div>
                    <div class="timeline-time">{{ formatTime(insightsStats.last_generation_time) }}</div>
                  </div>
                </div>
                <div class="timeline-item" v-if="highPriorityInsights.length > 0">
                  <div class="timeline-dot priority"></div>
                  <div class="timeline-content">
                    <div class="timeline-title">识别关键问题</div>
                    <div class="timeline-desc">发现 {{ highPriorityInsights.length }} 个高优先级问题</div>
                    <div class="timeline-time">{{ formatTime(insightsStats.last_generation_time) }}</div>
                  </div>
                </div>
                <div class="timeline-item" v-if="feedbackStats.total_feedback > 0">
                  <div class="timeline-dot data"></div>
                  <div class="timeline-content">
                    <div class="timeline-title">数据源更新</div>
                    <div class="timeline-desc">处理 {{ feedbackStats.total_feedback }} 条用户反馈</div>
                    <div class="timeline-time">刚刚</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </a-col>
      </a-row>
    </div>

    <!-- 数据配置弹窗 -->
    <a-modal
      v-model:open="showDataConfig"
      title="数据源配置"
      width="600px"
      @ok="updateDataConfig"
    >
      <a-form
        :model="dataConfig"
        :label-col="{ span: 6 }"
        :wrapper-col="{ span: 18 }"
      >
        <a-form-item label="反馈数量限制">
          <a-input-number
            v-model:value="dataConfig.feedback_limit"
            :min="10"
            :max="500"
            style="width: 100%"
          />
        </a-form-item>
        <a-form-item label="包含执行计划">
          <a-switch v-model:checked="dataConfig.include_action_plans" />
        </a-form-item>
        <a-form-item label="时间范围">
          <a-range-picker v-model:value="dataConfig.date_range" />
        </a-form-item>
        <a-form-item label="情感过滤">
          <a-select
            v-model:value="dataConfig.sentiment_filter"
            mode="multiple"
            placeholder="选择情感类型"
            style="width: 100%"
          >
            <a-select-option value="positive">正面</a-select-option>
            <a-select-option value="negative">负面</a-select-option>
            <a-select-option value="neutral">中性</a-select-option>
          </a-select>
        </a-form-item>
      </a-form>
    </a-modal>

    <!-- 反馈选择弹窗 -->
    <a-modal
      v-model:open="showFeedbackSelector"
      title="选择反馈数据"
      width="800px"
      @ok="generateInsightsFromSelected"
    >
      <div class="feedback-selector">
        <div class="selector-filters">
          <a-space>
            <a-input
              v-model:value="feedbackSearch"
              placeholder="搜索反馈内容"
              style="width: 200px"
            >
              <template #prefix>
                <SearchOutlined />
              </template>
            </a-input>
            <a-select
              v-model:value="feedbackCategoryFilter"
              placeholder="选择分类"
              style="width: 120px"
              allowClear
            >
              <a-select-option value="bug">问题</a-select-option>
              <a-select-option value="feature">功能</a-select-option>
              <a-select-option value="performance">性能</a-select-option>
              <a-select-option value="ui_ux">界面</a-select-option>
            </a-select>
          </a-space>
        </div>
        
        <div class="feedback-list">
          <a-table
            :columns="feedbackColumns"
            :data-source="filteredFeedbacks"
            :row-selection="feedbackRowSelection"
            :pagination="{ pageSize: 5 }"
            size="small"
          >
            <template #bodyCell="{ column, record }">
              <template v-if="column.key === 'sentiment'">
                <a-tag :color="getSentimentColor(record.sentiment)">
                  {{ getSentimentText(record.sentiment) }}
                </a-tag>
              </template>
            </template>
          </a-table>
        </div>
      </div>
    </a-modal>
  </div>
</template>

<script>
import { computed, ref, reactive, onMounted } from 'vue'
import { message } from 'ant-design-vue'
import { useInsightsStore } from '@/stores/insights'
import InsightsPanel from '@/components/InsightsPanel.vue'
import { feedbackAPI } from '@/utils/api'
import {
  BulbOutlined,
  PlayCircleOutlined,
  ReloadOutlined,
  EyeOutlined,
  ExclamationCircleOutlined,
  CheckCircleOutlined,
  ClockCircleOutlined,
  SettingOutlined,
  DatabaseOutlined,
  SelectOutlined,
  SearchOutlined,
  LineChartOutlined,
  TrophyOutlined
} from '@ant-design/icons-vue'

export default {
  name: 'InsightsManagement',
  components: {
    InsightsPanel,
    BulbOutlined,
    PlayCircleOutlined,
    ReloadOutlined,
    EyeOutlined,
    ExclamationCircleOutlined,
    CheckCircleOutlined,
    ClockCircleOutlined,
    SettingOutlined,
    DatabaseOutlined,
    SelectOutlined,
    SearchOutlined,
    LineChartOutlined,
    TrophyOutlined
  },
  setup() {
    const insightsStore = useInsightsStore()
    
    // 响应式数据
    const showDataConfig = ref(false)
    const showFeedbackSelector = ref(false)
    const selectedCategory = ref('all')
    const feedbackSearch = ref('')
    const feedbackCategoryFilter = ref(null)
    const selectedFeedbacks = ref([])
    
    // 数据配置
    const dataConfig = reactive({
      feedback_limit: 50,
      include_action_plans: true,
      date_range: null,
      sentiment_filter: []
    })
    
    // 模拟反馈数据
    const feedbackStats = reactive({
      total_feedback: 0,
      high_priority_count: 0,
      sentiment_distribution: {
        positive: 0,
        negative: 0,
        neutral: 0
      }
    })
    
    // 计算属性 - 使用 Pinia getters
    const isGenerating = computed(() => insightsStore.isGenerating)
    const allInsights = computed(() => insightsStore.allInsights)
    const allActionPlans = computed(() => insightsStore.allActionPlans)
    const highPriorityInsights = computed(() => insightsStore.highPriorityInsights)
    const urgentActionPlans = computed(() => insightsStore.urgentActionPlans)
    const insightsByType = computed(() => insightsStore.insightsByType)
    const actionPlansByPriority = computed(() => insightsStore.actionPlansByPriority)
    const insightsStats = computed(() => insightsStore.insightsStats)
    
    // 表格配置
    const feedbackColumns = [
      { title: '内容', dataIndex: 'text', key: 'text', ellipsis: true },
      { title: '情感', dataIndex: 'sentiment', key: 'sentiment', width: 80 },
      { title: '分类', dataIndex: 'category', key: 'category', width: 100 },
      { title: '时间', dataIndex: 'created_at', key: 'created_at', width: 150 }
    ]
    
    const feedbackRowSelection = {
      selectedRowKeys: selectedFeedbacks,
      onChange: (keys) => {
        selectedFeedbacks.value = keys
      }
    }
    
    // 模拟反馈数据
    const feedbacks = ref([])
    const filteredFeedbacks = computed(() => {
      return feedbacks.value.filter(feedback => {
        const matchSearch = !feedbackSearch.value || 
          feedback.text.toLowerCase().includes(feedbackSearch.value.toLowerCase())
        const matchCategory = !feedbackCategoryFilter.value || 
          feedback.category === feedbackCategoryFilter.value
        return matchSearch && matchCategory
      })
    })
    
    // 方法
    const generateInsights = async () => {
      try {
        await insightsStore.generateInsights()
        message.success('洞察生成成功')
        updateFeedbackStats()
      } catch (error) {
        message.error('洞察生成失败')
      }
    }
    
    const getDemo = async () => {
      try {
        await insightsStore.getInsightsDemo()
        message.success('演示数据加载成功')
        updateFeedbackStats()
      } catch (error) {
        message.error('演示数据加载失败')
      }
    }
    
    const refreshData = () => {
      // 刷新数据
      updateFeedbackStats()
      message.info('数据已刷新')
    }
    
    const refreshFeedbackData = async () => {
      try {
        // 从API获取真实反馈数据
        const response = await feedbackAPI.getFeedbacks({
          limit: 100,
          status: 'completed'  // 只获取已处理的反馈
        })
        
        if (response.data && response.data.data) {
          // 转换为前端需要的格式
          feedbacks.value = response.data.data.map(item => ({
            id: item.id,
            text: item.original_text || item.processed_text || '内容缺失',
            sentiment: item.sentiment || 'neutral',
            category: item.category || 'general',
            created_at: item.created_at || new Date().toISOString(),
            source: item.source || 'unknown',
            priority: item.priority || 'normal',
            analysis_result: item.analysis_result
          }))
          
          console.log(`从API获取到${feedbacks.value.length}条反馈数据`)
        } else {
          // 如果API返回空数据，使用演示数据
          feedbacks.value = generateMockFeedbacks()
          console.log('API返回空数据，使用演示数据')
        }
        
        updateFeedbackStats()
        message.success(`反馈数据已更新 (${feedbacks.value.length}条)`)
      } catch (error) {
        console.error('获取反馈数据失败:', error)
        // 失败时使用演示数据
        feedbacks.value = generateMockFeedbacks()
        updateFeedbackStats()
        message.warning('使用演示数据，请检查网络连接')
      }
    }
    
    const updateDataConfig = () => {
      insightsStore.updateGenerationConfig({
        feedback_limit: dataConfig.feedback_limit,
        include_action_plans: dataConfig.include_action_plans,
        filters: {
          date_range: dataConfig.date_range,
          sentiment: dataConfig.sentiment_filter
        }
      })
      showDataConfig.value = false
      message.success('数据配置已更新')
    }
    
    const generateInsightsFromSelected = async () => {
      if (selectedFeedbacks.value.length === 0) {
        message.warning('请选择至少一条反馈数据')
        return
      }
      
      try {
        await insightsStore.generateManualInsights(selectedFeedbacks.value)
        showFeedbackSelector.value = false
        message.success(`基于 ${selectedFeedbacks.value.length} 条反馈生成洞察成功`)
      } catch (error) {
        message.error('洞察生成失败')
      }
    }
    
    // 辅助函数
    const getInsightIcon = (type) => {
      const icons = {
        trend: LineChartOutlined,
        pattern: SearchOutlined,
        opportunity: TrophyOutlined,
        risk: ExclamationCircleOutlined
      }
      return icons[type] || EyeOutlined
    }
    
    const getInsightTypeText = (type) => {
      const texts = {
        trend: '趋势分析',
        pattern: '模式识别',
        opportunity: '机会发现',
        risk: '风险预警'
      }
      return texts[type] || '其他洞察'
    }
    
    const getInsightTypeColor = (type) => {
      const colors = {
        trend: '#1890ff',
        pattern: '#722ed1',
        opportunity: '#52c41a',
        risk: '#f5222d'
      }
      return colors[type] || '#666'
    }
    
    const getPriorityColor = (priority) => {
      const colors = {
        P0: 'red',
        P1: 'orange',
        P2: 'blue',
        P3: 'green'
      }
      return colors[priority] || 'default'
    }
    
    const getSentimentColor = (sentiment) => {
      const colors = {
        positive: 'green',
        negative: 'red',
        neutral: 'blue'
      }
      return colors[sentiment] || 'default'
    }
    
    const getSentimentText = (sentiment) => {
      const texts = {
        positive: '正面',
        negative: '负面',
        neutral: '中性'
      }
      return texts[sentiment] || sentiment
    }
    
    const formatTime = (time) => {
      if (!time) return '暂无'
      return new Date(time).toLocaleString()
    }
    
    const generateMockFeedbacks = () => {
      // 生成模拟反馈数据
      return Array.from({ length: 20 }, (_, i) => ({
        id: `feedback_${i + 1}`,
        text: `这是第 ${i + 1} 条用户反馈内容，包含了用户的真实想法和建议`,
        sentiment: ['positive', 'negative', 'neutral'][i % 3],
        category: ['bug', 'feature', 'performance', 'ui_ux'][i % 4],
        created_at: new Date(Date.now() - i * 24 * 60 * 60 * 1000).toISOString().split('T')[0]
      }))
    }
    
    const updateFeedbackStats = () => {
      feedbackStats.total_feedback = feedbacks.value.length
      feedbackStats.high_priority_count = Math.floor(feedbacks.value.length * 0.3)
      feedbackStats.sentiment_distribution = {
        positive: feedbacks.value.filter(f => f.sentiment === 'positive').length,
        negative: feedbacks.value.filter(f => f.sentiment === 'negative').length,
        neutral: feedbacks.value.filter(f => f.sentiment === 'neutral').length
      }
    }
    
    // 组件挂载
    onMounted(() => {
      refreshFeedbackData()
    })
    
    return {
      isGenerating,
      allInsights,
      allActionPlans,
      highPriorityInsights,
      urgentActionPlans,
      insightsByType,
      actionPlansByPriority,
      insightsStats,
      feedbackStats,
      showDataConfig,
      showFeedbackSelector,
      selectedCategory,
      feedbackSearch,
      feedbackCategoryFilter,
      dataConfig,
      feedbackColumns,
      feedbackRowSelection,
      filteredFeedbacks,
      generateInsights,
      getDemo,
      refreshData,
      refreshFeedbackData,
      updateDataConfig,
      generateInsightsFromSelected,
