<template>
  <div class="insights-panel">
    <!-- 洞察标题栏 -->
    <div class="insights-header">
      <h3 class="insights-title">
        <BulbOutlined class="title-icon" />
        智能洞察
      </h3>
      <div class="insights-actions">
        <a-button 
          type="primary" 
          size="small" 
          @click="generateInsights"
          :loading="isGenerating"
        >
          <RiseOutlined />
          生成洞察
        </a-button>
        <a-button 
          size="small" 
          @click="getDemo"
          :loading="isGenerating"
        >
          演示
        </a-button>
        <a-button 
          size="small" 
          @click="clearInsights"
          :disabled="!hasInsights"
        >
          清空
        </a-button>
      </div>
    </div>

    <!-- 错误提示 -->
    <div v-if="hasError" class="error-alert">
      <a-alert 
        :message="lastError" 
        type="error" 
        show-icon 
        closable 
        @close="clearError"
      />
    </div>

    <!-- 生成中状态 -->
    <div v-if="isGenerating" class="generating-state">
      <a-spin size="large">
        <template #indicator>
          <LoadingOutlined style="font-size: 24px; color: #1890ff" />
        </template>
      </a-spin>
      <div class="generating-text">正在生成洞察...</div>
    </div>

    <!-- 洞察内容 -->
    <div v-else-if="hasInsights" class="insights-content">
      <!-- 洞察列表 -->
      <div class="insights-section">
        <h4 class="section-title">
          <EyeOutlined />
          洞察分析
        </h4>
        <div class="insights-list">
          <div 
            v-for="(insight, index) in insights" 
            :key="index"
            class="insight-item"
            :class="`insight-${insight.insight_type}`"
          >
            <div class="insight-header">
              <div class="insight-type">
                <component :is="getInsightIcon(insight.insight_type)" />
                {{ getInsightTypeText(insight.insight_type) }}
              </div>
              <div class="insight-confidence">
                <a-progress 
                  :percent="Math.round(insight.confidence_score * 100)"
                  size="small"
                  :stroke-color="getConfidenceColor(insight.confidence_score)"
                />
              </div>
            </div>
            <div class="insight-title">{{ insight.title }}</div>
            <div class="insight-description">{{ insight.description }}</div>
            <div class="insight-meta">
              <a-tag :color="getImpactColor(insight.impact_level)">
                {{ getImpactText(insight.impact_level) }}
              </a-tag>
              <span class="insight-segments">
                影响用户: {{ insight.affected_user_segments?.join(', ') || '所有用户' }}
              </span>
            </div>
          </div>
        </div>
      </div>

      <!-- 执行计划 -->
      <div class="action-plans-section">
        <h4 class="section-title">
          <CheckCircleOutlined />
          执行计划
        </h4>
        <div class="action-plans-list">
          <div 
            v-for="(plan, index) in actionPlans" 
            :key="index"
            class="action-plan-item"
            :class="`priority-${plan.priority}`"
          >
            <div class="plan-header">
              <div class="plan-priority">
                <a-tag :color="getPriorityColor(plan.priority)">
                  {{ plan.priority }}
                </a-tag>
              </div>
              <div class="plan-effort">
                <ClockCircleOutlined />
                {{ plan.estimated_effort }}
              </div>
            </div>
            <div class="plan-title">{{ plan.title }}</div>
            <div class="plan-summary">{{ plan.summary }}</div>
            <div class="plan-meta">
              <div class="plan-owner">
                <UserOutlined />
                {{ plan.owner_team }}
              </div>
              <div class="plan-timeline">
                <CalendarOutlined />
                {{ plan.timeline }}
              </div>
            </div>
            <div class="plan-metrics">
              <div class="metrics-title">成功指标:</div>
              <div class="metrics-list">
                <a-tag 
                  v-for="metric in plan.success_metrics" 
                  :key="metric"
                  color="blue"
                >
                  {{ metric }}
                </a-tag>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 空状态 -->
    <div v-else class="empty-state">
      <a-empty description="暂无洞察数据">
        <template #image>
          <BulbOutlined style="font-size: 48px; color: #d9d9d9" />
        </template>
        <a-button type="primary" @click="generateInsights">
          生成洞察
        </a-button>
      </a-empty>
    </div>
  </div>
</template>

<script>
import { computed, onMounted } from 'vue'
import { message } from 'ant-design-vue'
import { useInsightsStore } from '@/stores/insights'
import {
  BulbOutlined,
  RiseOutlined,
  EyeOutlined,
  CheckCircleOutlined,
  LoadingOutlined,
  ClockCircleOutlined,
  UserOutlined,
  CalendarOutlined,
  TrophyOutlined,
  ExclamationCircleOutlined,
  LineChartOutlined,
  SearchOutlined
} from '@ant-design/icons-vue'

export default {
  name: 'InsightsPanel',
  components: {
    BulbOutlined,
    RiseOutlined,
    EyeOutlined,
    CheckCircleOutlined,
    LoadingOutlined,
    ClockCircleOutlined,
    UserOutlined,
    CalendarOutlined,
    TrophyOutlined,
    ExclamationCircleOutlined,
    LineChartOutlined,
    SearchOutlined
  },
  setup() {
    const insightsStore = useInsightsStore()
    
    // 计算属性
    const isGenerating = computed(() => insightsStore.isGenerating)
    const hasInsights = computed(() => insightsStore.allInsights.length > 0)
    const hasError = computed(() => insightsStore.hasError)
    const lastError = computed(() => insightsStore.lastError)
    const insights = computed(() => insightsStore.allInsights)
    const actionPlans = computed(() => insightsStore.allActionPlans)
    
    // 方法
    const generateInsights = async () => {
      try {
        await insightsStore.generateInsights()
        message.success('洞察生成成功')
      } catch (error) {
        message.error('洞察生成失败')
      }
    }
    
    const getDemo = async () => {
      try {
        await insightsStore.getInsightsDemo()
        message.success('演示数据加载成功')
      } catch (error) {
        message.error('演示数据加载失败')
      }
    }
    
    const clearInsights = () => {
      insightsStore.clearInsights()
      message.info('洞察已清空')
    }
    
    const clearError = () => {
      insightsStore.error = null
    }
    
    // 辅助函数
    const getInsightIcon = (type) => {
      const icons = {
        trend: LineChartOutlined,
        pattern: SearchOutlined,
        opportunity: TrophyOutlined,
        risk: ExclamationCircleOutlined
      }
      return icons[type] || EyeOutlined
    }
    
    const getInsightTypeText = (type) => {
      const texts = {
        trend: '趋势',
        pattern: '模式',
        opportunity: '机会',
        risk: '风险'
      }
      return texts[type] || '洞察'
    }
    
    const getConfidenceColor = (score) => {
      if (score >= 0.8) return '#52c41a'
      if (score >= 0.6) return '#faad14'
      return '#ff4d4f'
    }
    
    const getImpactColor = (level) => {
      const colors = {
        high: 'red',
        medium: 'orange',
        low: 'blue'
      }
      return colors[level] || 'default'
    }
    
    const getImpactText = (level) => {
      const texts = {
        high: '高影响',
        medium: '中影响',
        low: '低影响'
      }
      return texts[level] || level
    }
    
    const getPriorityColor = (priority) => {
      const colors = {
        P0: 'red',
        P1: 'orange',
        P2: 'blue',
        P3: 'green'
      }
      return colors[priority] || 'default'
    }
    
    // 组件挂载时检查健康状态
    onMounted(async () => {
      try {
        await insightsStore.checkInsightsHealth()
      } catch (error) {
        console.warn('洞察服务健康检查失败:', error)
      }
    })
    
    return {
      isGenerating,
      hasInsights,
      hasError,
      lastError,
      insights,
      actionPlans,
      generateInsights,
      getDemo,
      clearInsights,
      clearError,
      getInsightIcon,
      getInsightTypeText,
      getConfidenceColor,
      getImpactColor,
      getImpactText,
      getPriorityColor
    }
  }
}
</script>

